#bitcoin {
  grid-area: bitcoin;
}
#monero {
  grid-area: monero;
}
#ethereum {
  grid-area: ethereum;
}

.price-container .title {
  grid-area: title;
}


.crypto-container {
  display: grid;
  justify-items: center;
  grid-template-areas: 
  "title"
  "bitcoin"
  "monero"
  "ethereum";
}

.title {
  text-align: center;
}

.price-container {
  padding: 2ch;
  border: 1px solid white;
  margin-bottom: 2ch;
  width: 25%;
  text-align: center;
  background: #4a4a4a;
}

@media (max-width: 799px) {
  .price-container {
    width: 80%;
  }
}


.total-owned {
  text-align: center;
}